document.addEventListener("DOMContentLoaded", function () {

  ['load', 'resize'].forEach((event) => {
    window.addEventListener(event, function () {
        const header = document.querySelector('.header__nav-wrapper');
        const headerAll = document.querySelector('.header');
        //const headerPl = document.querySelector('.plashka');
        let headerHeight = header.clientHeight;
        let headerAllHeight = headerAll.clientHeight;
        //let headerPlHeight = headerPl.clientHeight;
        const main = document.querySelector('.main');
        window.onscroll = function (e) {
            if (window.innerWidth > 1050) {
                if (window.scrollY > (headerAllHeight - headerHeight)) {
                    if (!headerAll.classList.contains('fixed')) {
                        headerAll.classList.add('fixed');
                    }
                    main.style.marginTop = headerAllHeight + 'px';
                }
                else {
                    headerAll.classList.remove('fixed');
                    main.removeAttribute("style");
                }
            }
            else {
                headerAll.classList.remove('fixed');
                main.removeAttribute("style");
            }

        };
    })
  })

  const burgerMenu = document.querySelector('.menu-burger');
  const burgerMenuSvg = document.querySelector('.menu-burger svg');
  const headerMobile = document.querySelector('.header__bot');
  let headerHeight = document.querySelector('.header').offsetHeight;
  if (burgerMenu) {
    burgerMenu.addEventListener("click", () => {
      let headerHeight = document.querySelector('.header').offsetHeight;
      if (headerMobile.classList.contains("active")) {
        headerMobile.removeAttribute('style');
      }
      else {
        headerMobile.style.height = 'calc(100vh - ' + headerHeight + 'px)';
      }
      headerMobile.classList.toggle("active");
      burgerMenu.classList.toggle("active");
      burgerMenuSvg.classList.toggle("active");
      const header =  document.querySelector('.header');
      header.classList.toggle("active");
      document.querySelector('html').classList.toggle('burger-lock');
    });
  }

  const panelItems = document.querySelectorAll('.panel-title');
  if (panelItems.length > 0) {
    panelItems.forEach((elem) => {
      bodyItem = elem.nextElementSibling;
      elem.addEventListener('click', function (e) {
        if (elem.classList.contains('panel-active')) {
          elem.classList.remove('panel-active');
          elem.nextElementSibling.classList.remove('active');
          elem.previousElementSibling.classList.remove('link-active');
        } else {
          elem.classList.add('panel-active');
          elem.nextElementSibling.classList.add('active');
          elem.previousElementSibling.classList.add('link-active');
        }
      })
    })
  }

  const hideItems = document.querySelectorAll('.hide-items');
  if (hideItems.length > 0) {
      hideItems.forEach((elem) => {
          const hideItem = elem.querySelectorAll('.hide-item');
          const hideTitles = elem.querySelectorAll('.hide-item__title');
          const hideContents = elem.querySelectorAll('.hide-item__content');
          hideItem.forEach((item) => {
              let title = item.querySelector('.hide-item__title');
              let content = item.querySelector('.hide-item__content');
              if (title && content) { 
                  title.addEventListener('click', () => {
                      if (title.classList.contains('active')) {
                          title.classList.remove('active');
                          content.classList.remove('active');
                          content.style.maxHeight = '0';
                      } else {
                          hideTitles.forEach((element) => {
                              element.classList.remove('active');
                          });
                          hideContents.forEach((element) => {
                              element.classList.remove('active');
                              element.style.maxHeight = '0';
                          });
                          let height = content.querySelector('.hide-item_max-height').offsetHeight;
                          title.classList.add('active');
                          content.classList.add('active');
                          content.style.maxHeight = height + 'px';
                      }
                  });
              }
          });
      });
  }

  const navs = document.querySelectorAll('.navigation__wrapper, .karta__wrapper');
  if (navs.length > 0) {
      navs.forEach((elem) => {
          const hideItem = elem.querySelectorAll('.navigation__inner, .karta__inner');
          const hideTitles = elem.querySelectorAll('.nav-btn');
          const hideContents = elem.querySelectorAll('.navigation__block, .karta__block');
          hideItem.forEach((item) => {
              let title = item.querySelector('.nav-btn');
              let content = item.querySelector('.navigation__block, .karta__block');
              if (title && content) { 
                  title.addEventListener('click', () => {
                      if (title.classList.contains('active')) {
                          title.classList.remove('active');
                          content.classList.remove('active');
                          let height = content.querySelector('.navigation__items, .karta__items').offsetHeight;
                          content.style.maxHeight = (height + 20) + 'px';
                          setTimeout(() => {
                            content.style.maxHeight = '0';
                          }, 400);
                      } else {
                          /*hideTitles.forEach((element) => {
                              element.classList.remove('active');
                          });
                          hideContents.forEach((element) => {
                              element.classList.remove('active');
                              element.style.maxHeight = '0';
                          });*/
                          let height = content.querySelector('.navigation__items, .karta__items').offsetHeight;
                          title.classList.add('active');
                          content.classList.add('active');
                          content.style.maxHeight = (height + 20) + 'px';
                          setTimeout(() => {
                            content.style.maxHeight = '100%';
                          }, 400);
                      }
                  });
              }
          });
      });
  }

  // Popups
  let scrollWidthFunc = () => {
    let scrollWidth = window.innerWidth - document.body.clientWidth;
    document.querySelector('html').style.paddingRight = scrollWidth + 'px';
    if (document.querySelector('header').classList.contains('fixed')) {
        document.querySelector('header').style.paddingRight = scrollWidth + 'px';
    }
  }
  function popupClose(popupActive) {
      popupActive.classList.remove('open');
      setTimeout(() => {
          popupActive.classList.contains("open") || popupActive.classList.remove("active");
      }, 400);
      document.body.classList.remove('lock');
      document.querySelector('html').style.paddingRight = 0;
      document.querySelector('html').classList.remove('lock');
      document.querySelector('header').removeAttribute('style');
  }
  const popupOpenBtns = document.querySelectorAll('.popup-btn');
  const popups = document.querySelectorAll('.popup');
  const closePopupBtns = document.querySelectorAll('.close-popup');
  closePopupBtns.forEach(function (el) {
      el.addEventListener('click', function (e) {
          popupClose(e.target.closest('.popup'));
      });
  });
  popupOpenBtns.forEach(function (el) {
      el.addEventListener('click', function (e) {
          e.preventDefault();
          const path = e.currentTarget.dataset.path;
          const currentPopup = document.querySelector(`[data-target="${path}"]`);
          if (currentPopup) {
              popups.forEach(function (popup) {
                  popupClose(popup);
                  popup.addEventListener('click', function (e) {
                      if (!e.target.closest('.popup__content')) {
                          popupClose(e.target.closest('.popup'));
                      }
                  });
              });
              currentPopup.classList.add('active');
              setTimeout(() => {
                  currentPopup.classList.add("open");
              }, 10);
              if (currentPopup.getAttribute('data-target') == 'reviews') {
                  currentPopup.querySelector('.popup__rev').innerHTML = '<div class="reviews__title">' + e.target.closest('.reviews-slider__item').querySelector('.reviews__title').innerHTML + '</div><div class="reviews__text">' + e.target.closest('.reviews-slider__item').querySelector('.reviews__text').innerHTML + '</div>';
              }
              if (currentPopup.getAttribute('data-target') == 'popup_price') {
                  currentPopup.querySelector('.popup__form-title').innerHTML = e.target.closest('.price__row').querySelector('.price__name').innerHTML;
                  currentPopup.querySelector('.popup__form-subtitle').innerHTML = e.target.closest('.price__row').querySelector('.price__cost').innerHTML;
              }
              if (currentPopup.getAttribute('data-target') == 'popup_2' && e.target.closest('.doctor__inner')) {
                  currentPopup.querySelector('.popup__form-title').innerHTML = 'Записаться к врачу:<br>' + e.target.closest('.doctor__inner').querySelector('.section-title').innerHTML;
                  currentPopup.querySelector('.popup__form-subtitle').innerHTML = 'Оставьте заявку и мы вам перезвоним';
              }
              scrollWidthFunc();
              document.querySelector('html').classList.add('lock');
          }
      });
  });
  /* end popups */

  const headerPhonesBtn = document.querySelector('.header__phones-btn');
  if (headerPhonesBtn) {
    headerPhonesBtn.addEventListener('click', function (e) {
      if (!headerPhonesBtn.closest('.header__phones').classList.contains('active')) {
        headerPhonesBtn.closest('.header__phones').classList.add('active');
      } else {
        headerPhonesBtn.closest('.header__phones').classList.remove('active');
      }
    });
  }

  const mainSliderCheck = document.querySelectorAll('.main-swiper');
  if (mainSliderCheck.length > 0) {
    mainSliderCheck.forEach((slider) => {
        const swipermain = new Swiper(slider.querySelector('.main-service__swiper'), {
            loop: true,
            pagination: {
              el: '.main-service__swiper-pagination',
              clickable: true,
            },
            grabCursor: true,
            slidesPerView: 1,
            spaceBetween: 20,
        });
    })
  }

  const maboutSliderCheck = document.querySelectorAll('.mabout__top');
  if (maboutSliderCheck.length > 0) {
    maboutSliderCheck.forEach((slider) => {
        const swipermabout = new Swiper(slider.querySelector('.mabout__swiper'), {
            pagination: {
              el: '.mabout__swiper-pagination',
              clickable: true,
            },
            slidesPerView: 1,
            spaceBetween: 20,
        });
    })
  }

  const servSliderCheck = document.querySelector('.services__swiper');
  if (servSliderCheck) {
    let swiperservices;
    function handleSwiper() {
      if (window.innerWidth < 1050) {
        if (!swiperservices) {
          swiperservices = new Swiper(document.querySelector('.services__swiper'), {
            pagination: {
                el: '.services__swiper-pagination',
                clickable: true,
              },
            slidesPerView: 1,
            spaceBetween: 25,
          });
        }
      } else {
        if (swiperservices) {
          swiperservices.destroy(true, true);
          swiperservices = null;
        }
      }
    }
    handleSwiper();
    window.addEventListener('resize', handleSwiper);
  }

  const docslSliderCheck = document.querySelectorAll('.doc-slider');
  if (docslSliderCheck.length > 0) {
    docslSliderCheck.forEach((slider) => {
        const swiperdocsl = new Swiper(slider.querySelector('.doc-slider__swiper'), {
            navigation: {
              nextEl: slider.querySelector('.doc-slider__swiper-button-next'),
              prevEl: slider.querySelector('.doc-slider__swiper-button-prev'),
            },
            pagination: {
              el: '.doc-slider__swiper-pagination',
              clickable: true,
            },
            slidesPerView: 1,
            spaceBetween: 20,
            breakpoints: {
              1050: {
                slidesPerView: 3,
                spaceBetween: 20,   
              },
              600: {
                slidesPerView: 2,
                spaceBetween: 20,   
              },
            }
        });
    })
  }

  const licslSliderCheck = document.querySelectorAll('.lic-slider');
  if (licslSliderCheck.length > 0) {
    licslSliderCheck.forEach((slider) => {
        const swiperlicsl = new Swiper(slider.querySelector('.lic-slider__swiper'), {
            navigation: {
              nextEl: slider.querySelector('.lic-slider__swiper-button-next'),
              prevEl: slider.querySelector('.lic-slider__swiper-button-prev'),
            },
            pagination: {
              el: '.lic-slider__swiper-pagination',
              clickable: true,
            },
            slidesPerView: 1,
            spaceBetween: 20,
            breakpoints: {
              700: {
                slidesPerView: 3,
                spaceBetween: 20,   
              },
              420: {
                slidesPerView: 2,
                spaceBetween: 20,   
              },
            }
        });
    })
  }

  const stepsSliderCheck = document.querySelector('.steps__inner');
  if (stepsSliderCheck) {
    let swipersteps;
    function handleSwiper() {
      if (window.innerWidth < 1050) {
        if (!swipersteps) {
          swipersteps = new Swiper(document.querySelector('.steps__inner'), {
            pagination: {
                el: '.steps__swiper-pagination',
                clickable: true,
              },
            slidesPerView: 1,
            spaceBetween: 25,
            breakpoints: {
              700: {
                slidesPerView: 3,
                spaceBetween: 20,   
              },
            }
          });
        }
      } else {
        if (swipersteps) {
          swipersteps.destroy(true, true);
          swipersteps = null;
        }
      }
    }
    handleSwiper();
    window.addEventListener('resize', handleSwiper);
  }

  const statislSliderCheck = document.querySelectorAll('.stati-slider');
  if (statislSliderCheck.length > 0) {
    statislSliderCheck.forEach((slider) => {
        const swiperstatisl = new Swiper(slider.querySelector('.stati-slider__swiper'), {
            navigation: {
              nextEl: slider.querySelector('.stati-slider__swiper-button-next'),
              prevEl: slider.querySelector('.stati-slider__swiper-button-prev'),
            },
            pagination: {
              el: '.stati-slider__swiper-pagination',
              clickable: true,
            },
            slidesPerView: 1,
            spaceBetween: 20,
            breakpoints: {
              1050: {
                slidesPerView: 3,
                spaceBetween: 20,   
              },
              600: {
                slidesPerView: 2,
                spaceBetween: 20,   
              },
            }
        });
    })
  }

  const specialSliderCheck = document.querySelectorAll('.special-slider');
  if (specialSliderCheck.length > 0) {
    specialSliderCheck.forEach((slider) => {
        const swiperspecial = new Swiper(slider.querySelector('.special-slider__swiper'), {
            navigation: {
              nextEl: slider.querySelector('.special-slider__swiper-button-next'),
              prevEl: slider.querySelector('.special-slider__swiper-button-prev'),
            },
            pagination: {
              el: '.special-slider__swiper-pagination',
              clickable: true,
            },
            slidesPerView: 1,
            spaceBetween: 20,
            breakpoints: {
              1050: {
                slidesPerView: 2,
                spaceBetween: 20,   
              },
            }
        });
    })
  }

  const reviewsSliderCheck = document.querySelectorAll('.reviews-slider');
  if (reviewsSliderCheck.length > 0) {
    reviewsSliderCheck.forEach((slider) => {
        const swiperreviews = new Swiper(slider.querySelector('.reviews-slider__swiper'), {
            navigation: {
              nextEl: slider.querySelector('.reviews-slider__swiper-button-next'),
              prevEl: slider.querySelector('.reviews-slider__swiper-button-prev'),
            },
            pagination: {
              el: '.reviews-slider__swiper-pagination',
              clickable: true,
            },
            slidesPerView: 1,
            spaceBetween: 20,
            breakpoints: {
              1050: {
                slidesPerView: 3,
                spaceBetween: 20,   
              },
              600: {
                slidesPerView: 2,
                spaceBetween: 20,   
              },
            }
        });
    })
  }
  
  const reviewsItems = document.querySelector('.reviews-slider__wrapper');
  if (reviewsItems) {
    ['load', 'resize'].forEach((event) => {
      window.addEventListener(event, function () {
        const slide = reviewsItems.querySelectorAll('.reviews-slider__item');
        slide.forEach((e) => {
          const text = e.querySelector('.reviews__text');
          let textHeight = text.scrollHeight;
          if (e.querySelector('.reviews__more')) {
            if (textHeight <= 200) {
              e.querySelector('.reviews__more').style.display = 'none';
            } else {
              e.querySelector('.reviews__more').removeAttribute("style");
            }
          }
        });
      });
    });
  }

  const tabsBtn = document.querySelectorAll('.price__tab');
  const tabsBlock = document.querySelectorAll('.price__block');
  if (tabsBtn.length > 0) {
      for (let i = 0; i < tabsBtn.length; i++) {
          tabsBtn[i].addEventListener('click', ()=> {
              if (!tabsBtn[i].classList.contains('active')) {
                  tabsBlock.forEach(elem => {
                      elem.classList.remove('active');
                  })
                  tabsBtn.forEach(elem => {
                      elem.classList.remove('active');
                  })
                  tabsBtn[i].classList.add('active');
                  tabsBlock[i].classList.add('active');
              }
          })
      }
  }
  if (tabsBtn.length > 0) {
      tabsBtn[0].click();
  }

  const rms = document.querySelectorAll('.read-more__wrapper');
  if (rms.length > 0) {
    rms.forEach((e) => {
      const rm = e.querySelectorAll('*:not(.read-more)');
      function rmItems (elems) {
        let rmCheck = false;
        let rmHeight = 0;
        elems.forEach((el) => {
          if (!rmCheck && rmHeight <= 200) {
            rmHeight = rmHeight + el.clientHeight;
          } else {
            rmCheck = true;
            el.classList.add('hide');
          }
        })
      }
      rmItems(rm);
      const rmBtn = e.querySelector('.read-more');
      rmBtn.addEventListener('click', function (e) {
        if (!rmBtn.classList.contains('active')) {
          rmBtn.classList.add('active');
          rmBtn.innerHTML = 'Скрыть <svg xmlns="http://www.w3.org/2000/svg" width="16" height="9" viewBox="0 0 16 9" fill="none"><path d="M7.54742 0.225126L0.909923 7.19137C0.799117 7.30761 0.737304 7.46204 0.737304 7.62262C0.737304 7.78321 0.799117 7.93764 0.909923 8.05387L0.917423 8.06137C0.97114 8.11792 1.0358 8.16294 1.10746 8.19371C1.17913 8.22448 1.25631 8.24034 1.3343 8.24034C1.41229 8.24034 1.48947 8.22448 1.56113 8.19371C1.6328 8.16294 1.69746 8.11792 1.75117 8.06137L8.00117 1.50138L14.2487 8.06137C14.3024 8.11792 14.367 8.16294 14.4387 8.19371C14.5104 8.22448 14.5876 8.24034 14.6655 8.24034C14.7435 8.24034 14.8207 8.22448 14.8924 8.19371C14.964 8.16294 15.0287 8.11792 15.0824 8.06137L15.0899 8.05387C15.2007 7.93764 15.2625 7.78321 15.2625 7.62263C15.2625 7.46204 15.2007 7.30761 15.0899 7.19138L8.45242 0.225126C8.39405 0.163861 8.32385 0.115087 8.24607 0.0817608C8.16828 0.0484349 8.08455 0.0312506 7.99992 0.0312506C7.9153 0.0312506 7.83156 0.0484348 7.75378 0.0817608C7.676 0.115087 7.6058 0.163861 7.54742 0.225126Z" fill="#2684FC"/></svg>';
          rm.forEach((el) => {
            el.classList.remove('hide');
          })
        } else {
          rmBtn.classList.remove('active');
          rmBtn.innerHTML = 'Читать далее <svg xmlns="http://www.w3.org/2000/svg" width="16" height="9" viewBox="0 0 16 9" fill="none"><path d="M8.45258 8.27512L15.0901 1.30887C15.2009 1.19264 15.2627 1.03821 15.2627 0.87762C15.2627 0.717032 15.2009 0.562607 15.0901 0.446372L15.0826 0.43887C15.0289 0.382326 14.9642 0.337302 14.8925 0.306535C14.8209 0.275767 14.7437 0.259902 14.6657 0.259902C14.5877 0.259902 14.5105 0.275767 14.4389 0.306535C14.3672 0.337302 14.3025 0.382326 14.2488 0.43887L7.99883 6.99887L1.75133 0.438869C1.69761 0.382325 1.63295 0.337301 1.56129 0.306534C1.48962 0.275766 1.41244 0.259901 1.33445 0.259901C1.25646 0.259901 1.17928 0.275766 1.10762 0.306534C1.03595 0.337301 0.971293 0.382325 0.917577 0.438869L0.910075 0.446371C0.799269 0.562606 0.737456 0.717031 0.737456 0.877619C0.737456 1.03821 0.799269 1.19263 0.910075 1.30887L7.54758 8.27512C7.60595 8.33638 7.67615 8.38516 7.75393 8.41848C7.83172 8.45181 7.91545 8.46899 8.00008 8.46899C8.0847 8.46899 8.16844 8.45181 8.24622 8.41848C8.324 8.38516 8.3942 8.33638 8.45258 8.27512Z" fill="#5BC5B1"/></svg>';
          rmItems(rm);
        }
      })
    })
  }



  // о нас в цифрах
  const counters = document.querySelectorAll(".counter-js");

  const animateCounter = (el, target) => {
    let start = 0;
    const duration = 1500; // 1.5 сек
    const startTime = performance.now();

    const update = (currentTime) => {
      const elapsed = currentTime - startTime;
      const progress = Math.min(elapsed / duration, 1);
      const value = Math.floor(progress * target);

      el.textContent = value;

      if (progress < 1) {
        requestAnimationFrame(update);
      } else {
        el.textContent = target; 
      }
    };

    requestAnimationFrame(update);
  };

  const observer = new IntersectionObserver(entries => {
    entries.forEach(entry => {
      if (entry.isIntersecting) {
        const el = entry.target;
        const target = parseInt(el.textContent, 10); 
        el.textContent = "0"; 
        animateCounter(el, target);
        observer.unobserve(el);
      }
    });
  }, { threshold: 0.5 });

  counters.forEach(counter => observer.observe(counter));

});


